import React from "react";
import PropTypes from "prop-types";
import "./Movie.css";

/* 여기서 왜 title이 아니라 id.title을 해야지 원하는 결과가 나오는걸까? */
function Movie(id, year, title, summary, poster, genres) {
  return (
    <div className="movie">
      <img src={id.poster} alt={id.title} title={id.title} />
      <div className="movie__data">
        <h3 className="movie__title">{id.title}</h3>
        <h5 className="movie__year">{id.year}</h5>
        <ul className="movie__genres">
          {id.genres.map((genre, index) => (
            <li key={index} className="genres_genre">
              {genre}
            </li>
          ))}
        </ul>
        <p className="movie__summary">{id.summary.slice(0, 180)}...</p>
      </div>
    </div>
  );
}

Movie.propTypes = {
  id: PropTypes.number.isRequired,
  year: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  summary: PropTypes.string.isRequired,
  poster: PropTypes.string.isRequired,
  genres: PropTypes.arrayOf(PropTypes.string).isRequired,
};

export default Movie;
